@import url(https://fonts.googleapis.com/css2?family=Nunito:ital,wght@0,800;1,600;1,700&display=swap);
/* Mudar fonte cor de fundo */

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    color: #fff;
    background: #ebf2f5;
}

body, input, button, textarea {
    font: 600 18px 'Nunito', sans-serif;
}

.btn {
    width: 80px;
    height: 80px;
    border-radius: 30px;

    display: flex;
    align-items: center;
    justify-content: center;

    transition: background .2s;
}
#page-landing {
    width: 100vw;
    height: 100vh;
    background: linear-gradient(329.54deg, #29B6D1 0%, #00C7C7 100%);

    display: flex;
    justify-content: center;
    align-items: center;
}

#page-landing .content-wrapper {
    position: relative;

    width: 100%;
    max-width: 1100px;

    height: 100%;
    max-height: 680px;

    display: flex;
    align-items: flex-start;
    flex-direction: column;
    justify-content: space-between;

    background: url(/myhouse-web/static/media/ilustration.91c8d1af.svg) no-repeat 80% center;
}

#page-landing .content-wrapper main {
    max-width: 350px;
}

#page-landing .content-wrapper main h1 {
    font-size: 76px;
    font-weight: 900;
    line-height: 70px;
}

#page-landing .content-wrapper main p {
    margin-top: 40px;
    font-size: 24px;
    line-height: 34px;
}

.content-wrapper .location {
    position: absolute;
    top: 0;
    right: 0;

    font-size: 24px;
    line-height: 34px;

    display: flex;
    align-items: flex-end;
    flex-direction: column;
}

.content-wrapper .location strong {
    font-weight: 800;
}

.content-wrapper .enter-app {
    position: absolute;
    right: 0;
    bottom: 0;
    
    background: #ffd666;
}

.content-wrapper .enter-app:hover {
    background: #96feff;
}

* {
    box-sizing: border-box;
  }
  
  body {
    margin: 0;
  }
  
  .container {
    width: 100%;
    height: 100%;
    
    font-family: Arial, sans-serif;
    
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  
  .form {
    background: #434758;
    padding: 5px;
    border-radius: 7px;
  }
  
  .form__field {
    border: 1px solid #dcdcdc;
    border-radius: 5px;
    color: #333;
    font-size: 1.2rem;
    padding: .5rem 1rem;
    width: 100%;
  }
  
  .form__field:focus {
    border-color: #a3f7ff;
    box-shadow: 0 0 7px #a3f7ff;
    outline: none;
  }
  
  .list {
    margin: 0;
    padding: 1rem;
  }
  
  .list__item {
    list-style: none;
  }
  
  .list__item.list__item--mine {
    text-align: right;
  }
  
  .message {
    border: 1px solid transparent;
    border-radius: 5px;
    display: inline-block;
    list-style: none;
    margin-bottom: 1rem;
    padding: .5rem 1rem;
  }
  
  .message.message--mine {
    background: #c3e88d;
    border-color: #82be27;
    text-align: right;
  }
  
  .message.message--other {
    background: #89ddff;
    border-color: #1abeff;
  }
  
#container-controls {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
    grid-column-gap: 10px;
    -webkit-column-gap: 10px;
            column-gap: 10px;
    grid-row-gap: 10px;
    row-gap: 10px;
}
#container-controls .control {
    width: 100px;
    min-height: 150px;
    
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    
    border-radius: 7px;
    box-shadow: 5px 5px 18px 0px #797979;
    background: #fff;
    
    cursor: pointer;
}

#container-controls .control:first-child {
    margin-left: 0;
}
#container-controls .control:last-child {
    margin-right: 0;
}

#container-controls .control a,
#container-controls .control div {
    width: 100%;
    min-height: 100px;
    
    display: flex;
    align-items: center;
    justify-content: center;
}

#container-controls .control span {
    color: #545454;
}
#page-map {
    position: relative;

    width: 100vw;
    height: 100vh;

    display: flex;
}

#page-map aside {
    width: 440px;
    height: 100%;

    padding: 80px;

    background: linear-gradient(329.54deg, #15B6D6 0%, #15D6D6 100%);

    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

#page-map aside header h2 {
    margin-top: 64px;
    
    font-size: 40px;
    font-weight: 800;
    line-height: 42px;
}

#page-map aside header p {
    margin-top: 24px;
    font-size: 18px;
    line-height: 28px;
}

#page-map aside footer {
    display: flex;
    flex-direction: column;

    line-height: 24px;
}

#page-map .leaflet-container {
    z-index: 5;
}

#page-map .create-orphanage {
    position: absolute;
    right: 40px;
    bottom: 40px;

    width: 64px;
    height: 64px;

    border-radius: 20px;
    background: #15C3D6;
    
    z-index: 10;
}

#page-map .create-orphanage:hover {
    background: #17D6EB;
}
#page-template {
    position: relative;

    width: 100vw;
    height: 100vh;

    display: flex;
}

#page-template aside.menu {
    position: fixed;
    top: 50%;
    left: 0;
    transform: translateY(-50%);

    min-width: 70px;
    height: 95%;

    border-top-right-radius: 7px;
    border-bottom-right-radius: 7px;

    background: #fff;
    box-shadow: 4px 4px 12px #9e9e9e;

    display: flex;
    flex-direction: column;

    z-index: 1;
}

#page-template aside.menu:hover {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

#page-template aside.menu-bar {
    position: fixed;
    top: 26px;
    left: 100px;
    right: 20px;

    height: 70px;

    border-radius: 7px;

    background: #fff;
    box-shadow: 4px 4px 12px #9e9e9e;

    padding: 10px;

    display: flex;
    align-items: center;
    justify-content: flex-end;
}

#page-template aside.menu-bar .user{
    width: 50px;
    height: 50px;
    
    border-radius: 50%;
    background: #15c3d6;

    display: flex;
    align-items: center;
    justify-content: center;

    cursor: pointer;
}

#page-template .leaflet-container {
    z-index: 5;
}

#page-template .create-orphanage {
    position: absolute;
    right: 20px;
    bottom: 40px;

    width: 64px;
    height: 64px;

    border-radius: 20px;
    background: #15C3D6;
    
    z-index: 10;
}

#page-template .create-orphanage:hover {
    background: #17D6EB;
}

#page-template #content {
    position: absolute;
    top: 120px;
    left: 100px;
    right: 20px;
    bottom: 25px;
}

#page-template #content div {
    position: relative;

    width: 100%;
    height: 100%;
}

.menu .btn {
    width: 70px;
    height: 70px;
    border-radius: 0px;
}
.menu .btn:first-child {
    border-top-right-radius: 8px;
}
.menu .btn:hover, .menu .btn:hover svg {
    color: #fff !important;
    background: #00BCD4;
    /* background: #afafaf; */
}
